
import { gl } from './coregl';
import * as twgl from 'twgl.js'
import * as Vox from 'vox.js';
import {vector3} from '../core/v3';

var parser = new Vox.Parser();
export var voxelDataPromise = parser.parse("../data/chr_bow.vox");

function renderVoxelsToRGBAVolume(data:Array<Vox.Voxel>, palette: Array<Vox.Color>, size: vector3) : Uint8Array {
    const out = new Uint8Array(size.x*size.y*size.z*4);

    for(var i = 0; i < data.length; ++i) {
        // totally wrong...
        const d = data[i];        
        const c = palette[d.colorIndex];

        const base = (d.x + (d.y + (d.z * size.y)) * size.x) * 4;
        out[base+0] = c.r;
        out[base+1] = c.g;
        out[base+2] = c.b;
        out[base+3] = c.a;
    }
 
    /*for(var i = 0; i < out.length; ++i) {
        out[i] = i %80;
    }*/

    return out;

} 

export async function loadTextures() {
    var voxelData = await voxelDataPromise;

    voxelData.voxels; // voxel position and color data
    voxelData.size; // model size
    voxelData.palette; // palette data

    var rgbavolume = renderVoxelsToRGBAVolume(voxelData.voxels, voxelData.palette, voxelData.size);

    const textures = twgl.createTextures(gl, {
        // A 2x2 pixel texture from a JavaScript array
        color: {
          mag: gl.NEAREST,
          min: gl.NEAREST,
          src: <any>rgbavolume,
          width: voxelData.size.x,
          height: voxelData.size.y,
          depth: voxelData.size.z,
          type: gl.UNSIGNED_BYTE,
          format: gl.RGBA,
          target: gl.TEXTURE_3D, // important, to not get double-bind error of different type
//          internalFormat: gl.RG32F
        }
      });

    return textures;
}
