
//import * as twgl from 'twgl.js'
import * as twgl from 'twgl.js'
import { gl as gl_ } from './coregl';
import * as screenquad from './screenquad';
import {loadTextures} from './voxtotexture';


var gl: WebGL2RenderingContext = gl_ as WebGL2RenderingContext; // what is this all about?

async function main() {
  var voxTextures = await loadTextures();

  function render(time) {
    gl.canvas.width = window.innerWidth;
    gl.canvas.height = window.innerHeight;
    //twgl.resizeCanvasToDisplaySize(gl.canvas);
    gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);


    screenquad.draw(time, voxTextures.color, 0.0125, 0.5);

    requestAnimationFrame(render);
  } 
  requestAnimationFrame(render);

}


main();